@import './colors';

body {
  background: rgb(254, 214, 31);
  background: -moz-linear-gradient(
    180deg,
    rgba(254, 214, 31, 1) 0%,
    rgba(255, 236, 167, 1) 100%
  );
  background: -webkit-linear-gradient(
    180deg,
    rgba(254, 214, 31, 1) 0%,
    rgba(255, 236, 167, 1) 100%
  );
  background: linear-gradient(
    180deg,
    rgba(254, 214, 31, 1) 0%,
    rgba(255, 236, 167, 1) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#fed61f",endColorstr="#ffeca7",GradientType=1);
  height: 100vh;

  .container{
    overflow: hidden;
  }

  button[disabled]{
    opacity: 0.3 !important;
    cursor: not-allowed;
  }
}
